@use "../../styles/mixins.scss" as mixin;
@use "../../styles/variables.scss" as var;

.contacts {
  padding: 25px 0;

  &__title {
    margin: 0 0 20px 0;
    font-size: mixin.rem(20);
    font-weight: 400;
    text-align: center;

    @include mixin.min-tablet {
      margin-bottom: 40px;
      font-size: mixin.rem(30);
    }

    @include mixin.min-laptop {
      font-size: mixin.rem(34);
    }

    @include mixin.min-desktop-wide {
      margin-bottom: 80px;
      font-size: mixin.rem(50);
    }
  }

  &__phones {
    @include mixin.list-reset;
    margin-bottom: 20px;

    li {
      margin-bottom: 2px;

      &:last-child {
        margin-bottom: 0;
      }
    }
  }

  &__address {
    font-style: normal;
    text-align: center;
    font-size: mixin.rem(13);
    line-height: 1.3;

    &-text {
      margin: 0 0 15px 0;
    }

    &>a {
      display: inline-block;
      margin-bottom: 10px;
    }

    @include mixin.min-tablet {
      font-size: mixin.rem(22);

      &-text {
        margin-bottom: 20px;
      }

      &>a {
        margin-bottom: 20px;
      }

      h3 {
        font-size: mixin.rem(22);
      }
    }

    @include mixin.min-laptop {
      font-size: mixin.rem(26);

      h3 {
        font-size: mixin.rem(26);
      }
    }

    @include mixin.min-desktop-wide {
      font-size: mixin.rem(36);

      &-text {
        margin-bottom: 40px;
      }

      &>a {
        margin-bottom: 40px;
      }

      h3 {
        font-size: mixin.rem(36);
      }
    }
  }

  &__time {
    margin-bottom: 20px;
    line-height: 1;

    &-list {
      @include mixin.list-reset;
      padding-top: 5px;

      @include mixin.min-laptop {
        padding-top: 10px;

        li {
          margin-bottom: 2px;

          &:last-child {
            margin-bottom: 0;
          }
        }
      }

      @include mixin.min-desktop-wide {
        padding-top: 15px;
      }
    }

    p {
      margin: 0;
    }
  }

  &__socials {
    position: relative;
    padding-top: 20px;
    margin-top: 20px;

    &::before {
      @include mixin.pseudo;
      left: 50%;
      top: 0;
      height: 1px;
      width: 55%;
      background-color: var.$color-dim-gray;
      transform: translateX(-50%);
    }

    @include mixin.min-tablet {
      margin-top: 40px;
    }

    @include mixin.min-laptop {
      &::before {
        height: 3px;
      }
    }

    @include mixin.min-desktop {
      padding-top: 40px;
      margin-top: 60px;
    }

    @include mixin.min-desktop-wide {
      padding-top: 60px;
      margin-top: 80px;
    }
  }

  &__bottom {
    display: grid;

    @include mixin.min-laptop {
      grid-template-columns: 1fr 1fr 1fr;
      column-gap: 20px;

      a {
        @include mixin.interactive-opacity;
      }
    }
  }

  &__map {
    position: relative;
    width: 100%;
    padding-bottom: 59.38%;

    #contacts-map {
      position: absolute;
      left: 0;
      top: 0;
      width: 100%;
      height: 100%;
    }

    @include mixin.min-laptop {
      grid-column: span 2;
    }
  }

  &__image {
    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }

  @include mixin.min-tablet {
    padding: 40px 0;

    &__time {
      margin-bottom: 40px;
    }

    &__socials {
      margin-bottom: 30px;
    }

    &__bottom {
      padding-top: 30px;
    }
  }

  @include mixin.min-laptop {
    padding: 60px 0 40px;

    &__bottom {
      font-size: mixin.rem(22);
    }
  }

  @include mixin.min-desktop-wide {
    padding: 90px 0 40px;

    &__time {
      margin-bottom: 40px;
    }

    &__socials {
      margin-bottom: 60px;
    }

    &__bottom {
      padding-top: 60px;
    }
  }
}